@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poly&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body {
  display: flex;
  justify-content: center;
  background-color: white;
}

.body .info {
  display: block;
  width: 40%;
  margin: 30vh 0 0 14%;
  text-align: left;
  padding: 0 1ch;
}

.body .info .headline {
  font-size: 6ch;
  font-family: "Playfair";
  display: block;
  max-width: 300px;
  text-align: left;
  margin-bottom: 1ch;
}

.body .info .description {
  font-size: 2ch;
  font-family: "Poly";
  display: block;
  text-align: left;
  max-width: 350px;
}

.body .info button {
  margin-top: 3ch;
  padding: 1ch 2ch;
  border: none;
  cursor: pointer;
  background: linear-gradient(#bd1b28, #ff2717);
}

.body .info button a {
  text-decoration: none;
  color: white;
  font-family: "Playfair";
  font-size: 2.8ch;
}

.body .info button:hover {
  transition: 1s ease;
  background: linear-gradient(#817400, #700700, #001263);
}

.body .product {
  display: inline-block;
  width: 50%;
}

.body .product .images #circle {
  position: relative;
  width: 40vw;
  z-index: 0;
  left: -80px;
  top: 25vh;
}

.body .product .images #prod {
  position: absolute;
  top: 160px;
  right: 45vw;
  width: 50vw;
  z-index: 1;
}

@media only screen and (max-width: 700px) {
  .body {
    display: block;
    flex-direction: column;
    width: 100vw;
  }

  .body .product {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
  }

  .body .product .images #circle {
    display: none;
  }

  .body .product .images #prod {
    display: block;
    position: relative;
    width: 250px;
    left: 5%;
    top: 30px;
  }

  .body .info {
    display: block;
    width: 80%;
    margin-top: 50px;
    margin-bottom: 40px;
  }
}

.header .top-header {
  background-color: black;
  display: flex;
  justify-content: space-between;
  padding: 1vh 5% 1vh 10%;
  font-family: "Poly", serif;
  z-index: 4;
}

.header .top-header a {
  color: white;
  cursor: pointer;
  text-decoration: none;
}

.header .top-header a i {
  color: black;
  margin-right: 2ch;
}

.header .top-header a:hover,
.header .top-header a:hover i {
  color: rgb(0, 255, 21);
  transition: linear 1s ease;
}

.header .top-header .book-ride {
  text-decoration: none;
  color: rgb(236, 0, 0);
}

.header .top-header .book-ride i {
  color: black;
}

.header .top-header .book-ride:hover,
.header .top-header .book-ride:hover i {
  color: rgb(0, 255, 21);
  transition: linear 1s ease;
}

.header .options {
  display: flex;
  justify-content: space-between;
  list-style: none;
  background-color: black;
}

.header .top-header .options a i {
  color: rgb(202, 0, 0);
}

.header .options a {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.7ch;
  position: relative;
  margin: 0px 10px;
  text-decoration: none;
  color: white;
  font-family: "Playfair";
}

.header .options a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.175rem;
  left: 0;
  bottom: -2px;
  background: cyan;
}

.header .options a::after {
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transform-origin: 100% 0%;
          transform-origin: 100% 0%;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.header .options a:hover {
  color: cyan;
}

.header .options a:hover::after {
  color: cyan;
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.header .toggle-menu {
  display: none;
}

.header .bottom-header {
  display: flex;
  padding: 3vh 5vw 2vh 10vw;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  font-family: "Playfair Display", serif;
  z-index: 2;
}

.header .bottom-header .logo {
  padding: 8px;
  background-color: white;
}

.header .bottom-header .logo i {
  border: rgb(209, 0, 0) 8px solid;
  color: white;
}

.header .bottom-header .logo:hover {
  background: linear-gradient(indigo, blue, green, yellow, orange, red);
  cursor: pointer;
  -webkit-filter: brightness(130%);
          filter: brightness(130%);
  transition: 1s ease;
}

.header .bottom-header .logo:hover i {
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
}

@-webkit-keyframes animate {
  100% {
    -webkit-filter: hue-rotate(-360deg);
            filter: hue-rotate(-360deg);
  }
}

@keyframes animate {
  100% {
    -webkit-filter: hue-rotate(-360deg);
            filter: hue-rotate(-360deg);
  }
}

.header .bottom-header .options {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.header .bottom-header .options .btn {
  margin: 0 2vw;
  text-decoration: none;
  color: white;
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 2ch;
  position: relative;
  font-family: "Poly", serif;
}

.header .bottom-header .options .btn::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.175rem;
  left: 0;
  bottom: -10px;
  background: rgb(204, 0, 0);
}

.header .bottom-header .options .btn-center::after {
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.header .bottom-header .options .btn-center:hover::after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.header .bottom-header .options .dropdown {
  padding: 0 4ch;
  text-decoration: none;
  color: white;
  font-size: 2ch;
  font-weight: 500;
  cursor: pointer;
}

.header .bottom-header .options .dropdown i {
  color: rgb(212, 0, 0);
}

.header .bottom-header .dealership {
  padding: 8px 10px;
  background-color: rgb(218, 0, 0);
  border: none;
  font-size: 1.3vw;
  font-family: "Poly", serif;
  cursor: pointer;
  text-decoration: none;
  color: white;
  border-radius: 2px;
}

.header .bottom-header .dealership:hover {
  transition: 0.3s ease;
  border-radius: 10px;
}

.sub-menu {
  display: none;
  position: fixed;
  width: 93vw;
  height: 170px;
  padding: 0 3vw;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  background-color: rgb(243, 243, 243);
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 3;
}

.sub-menu .option {
  text-decoration: none;
  font-size: 2.5vw;
  font-family: "Poly", serif;
  color: gray;
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  position: relative;
}

.sub-menu .option::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.175rem;
  left: 0;
  bottom: 0;
  background: rgb(187, 0, 0);
}

.sub-menu .o-left::after {
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.sub-menu .o-left:hover {
  color: black;
}

.sub-menu .o-left:hover::after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

@media only screen and (max-width: 700px) {
  .header .top-header {
    display: block;
  }

  .header .top-header .support {
    display: block;
    margin-left: 3.4ch;
  }

  .header .top-header .book-ride {
    margin-left: 2ch;
    display: block;
  }

  .header .top-header .options {
    padding: 1ch 0;
  }

  .header .toggle-menu {
    display: block;
    background-color: black;
    padding: 0.5ch 0 0.5ch 3ch;
    border-top: 0.05vh solid white;
  }

  .header .toggle-menu i {
    display: block;
    color: white;
    cursor: pointer;
  }

  .header .bottom-header {
    display: none;
    flex-direction: column;
    margin-top: 0;
    padding-top: 0;
  }

  .header .bottom-header .logo {
    position: fixed;
    top: 10px;
    left: 10px;
    padding: 5px;
  }

  .header .bottom-header .logo i {
    font-size: 10px;
    border-width: 4px;
  }

  .header .bottom-header .options {
    flex-direction: column;
  }

  .header .bottom-header .options .btn,
  .dropdown {
    margin: 1ch 1ch;
    text-align: center;
    align-items: center;
    justify-content: center;
  }

  .header .bottom-header .dealership {
    display: block;
    margin-top: 1ch;
    width: 50%;
    text-align: center;
    font-size: 10px;
  }

  .sub-menu {
    display: none;
    height: 50px;
  }
}

